import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import {Rcomp1} from './App';
import {About} from './App';
import './index.css';
import r from './skate.webp';
import q from './r.jpeg';
import {Navto} from './App';
import {Contact} from './App';


import 'font-awesome/css/font-awesome.min.css';


ReactDOM.render(<Navto/>, document.getElementById('navbar'));
ReactDOM.render(
  <div><App imgUrl={r} colour="#FA9F1B" fcolor="#111111"/>

  		<Rcomp1 fa={"fa-github-square"} faname={'GITHUB'} butname={'Download CV'} butcolor={'btn-primary'} butlink={'https://www.hrii.tk/Resume.docx'} falink={'https://github.com/braceritchie'} facol={'text-dark'} fa1={"fa-html5"} fa2={"icon-ok"} fa3={"faFacebook"} colour={'#FFFFFF'} bg={'#292929'} skill1={'GITHUB'} ski1={25} skill2={'HTML5'} ski2={50} skill3={'JAVASCRIPT'} ski3={30} skill4={"PYTHON"} ski4={70}/>


  	
  		<About bgp={q}/>
      <Rcomp1 fa={"fa-linkedin"} faname={'LINKEDIN'} butname={'Hackerrank'} butcolor={'btn-success'} butlink={'https://www.hackerrank.com/hritik2357'} falink={'https://www.linkedin.com/in/hritik-tayade-58205b149'} facol={'text-primary'} colour={"#FFFFFF"} bg={'#292929'} skill1={'REACTJS'} ski1={20} skill2={'LINUX'} ski2={40} skill3={'JQUERY'} ski3={40} skill4={'C/C++'} ski4={80}/>
  		
  		<Contact/>
  </div>,
  document.getElementById('root')
);




